import React, { useCallback, useState } from 'react';
import { Field, Formik, ErrorMessage } from 'formik';
import {
  AuthButton,
} from '../AuthButton';
import { ToggleVisibilityWrapper } from '../index';
import {
  Form, ErrorMessageText, Input,
} from '../../../components';
import imgName from '../../../img/user.png';
import imgEmail from '../../../img/email.png';
import imgLock from '../../../img/lock.png';
import imgCheck from '../../../img/check.png';
import imgEye from '../../../img/eye.png';
import imgEyeSlash from '../../../img/eye-slash.png';
import { validationsScheme } from '../../validationScheme';

export function SignUpForm() {
  const [passVisibility, setPassVisibility] = useState(false);
  const [confirmPassVisibility, setConfirmPassVisibility] = useState(false);

  const changePassVisibility = useCallback(() => {
    if (passVisibility) {
      setPassVisibility(false);
    } else {
      setPassVisibility(true);
    }
  }, [passVisibility]);

  const changeConfirmPassVisibility = useCallback(() => {
    if (confirmPassVisibility) {
      setConfirmPassVisibility(false);
    } else {
      setConfirmPassVisibility(true);
    }
  }, [confirmPassVisibility]);

  return (
    <Formik
      initialValues={{
        firstName: '',
        lastName: '',
        email: '',
        password: '',
        confirmPassword: '',
      }}
      onSubmit={(values) => {
        console.log(values);
      }}
      validationSchema={validationsScheme}
    >
      {({
        handleSubmit,
      }) => (
        <Form>
          <Field
            as={Input}
            img={imgName}
            name="firstName"
            placeholder="First Name"
            type="text"
          />
          <ErrorMessage component={ErrorMessageText} name="firstName" />
          <Field
            as={Input}
            img={imgName}
            placeholder="Last Name"
            name="lastName"
            type="text"
          />
          <ErrorMessage component={ErrorMessageText} name="lastName" />
          <Field
            as={Input}
            img={imgEmail}
            placeholder="Email"
            name="email"
            type="text"
          />
          <ErrorMessage component={ErrorMessageText} name="email" />
          <ToggleVisibilityWrapper
            src={passVisibility ? imgEye : imgEyeSlash}
            onClick={changePassVisibility}
          />
          <Field
            as={Input}
            img={imgLock}
            placeholder="Password"
            type={passVisibility ? 'text' : 'password'}
            name="password"
          />
          <ToggleVisibilityWrapper
            src={confirmPassVisibility ? imgEye : imgEyeSlash}
            onClick={changeConfirmPassVisibility}
          />
          <ErrorMessage component={ErrorMessageText} name="password" />
          <Field
            as={Input}
            img={imgCheck}
            placeholder="Confirm Password"
            type={confirmPassVisibility ? 'text' : 'password'}
            name="confirmPassword"
          />
          <ErrorMessage component={ErrorMessageText} name="confirmPassword" />
          <div>
            <AuthButton onClick={handleSubmit} type="submit">
              Sign Up
            </AuthButton>
          </div>
        </Form>
      )}
    </Formik>
  );
}
